.nav-hide {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: calc(100vh - 100px);
  background-color: #add841;
  transform: translateY(-100%);
  transition: 0.3s;
  z-index: 1;
}


.nav-visible {
  transform: translateY(0);
  margin-top: 100px;
  padding-top: 40px;
}