.comments-form {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 15px;
  background-color: white;
  z-index: 3;
}

.comments-form__title {
  font-family: 'Vollkorn', serif;
  font-size: 30px;
  font-weight: 700;
  margin: 5px auto;
}

.comments-form__name-input,
.comments-form__comment-textarea {
  width: 100%;
  max-width: 450px;
  padding: 3px 5px;
  margin: 5px;
  outline: none;
  border: 1px solid #2c2c2c;
  font-family: 'Catamaran', sans-serif;
  line-height: 1.4;
  font-size: 20px;
  resize: none;
}

.comments-form__buttons {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-evenly;
  width: 100%;
  max-width: 500px;
  margin-top: 25px;
}

button.comments__button {
  width: 150px;
  margin-bottom: 10px;
}

button.comments__button--cancel {
  background-color: #32591A;
}

button.comments__button--cancel:hover {
  background-color: #4e882a;
}

.comments-form__name-input:focus::placeholder,
.comments-form__comment-textarea:focus::placeholder {
  color: transparent;
}

.comments-form__name-input:focus,
.comments-form__comment-textarea:focus {
  -webkit-box-shadow: 0px 0px 6px -3px rgb(90, 90, 90);
  -moz-box-shadow: 0px 0px 6px -3px rgb(90, 90, 90);
  box-shadow: 0px 0px 6px -3px rgb(90, 90, 90);
}

@media (min-width:800px) {
  .comments-form {
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 700px;
    min-height: 400px;
    padding: 50px 0;
    border: 1px solid #9fa1a2;
    background-color: #e5e5e6;
  }

  .comments-form__name-input,
  .comments-form__comment-textarea {
    width: 100%;
    max-width: 450px;
    padding: 3px 5px;
    margin: 5px;
    outline: none;
    border: 1px solid #969ea2;
    font-family: 'Catamaran', sans-serif;
    line-height: 1.4;
    font-size: 20px;
    resize: none;
  }
}